      right: 0;
      top: 0;
      bottom: 0;
      background-color: rgba(0, 0, 0, .5);
}
}
&__name {
    font-size: 1rem;
    font-weight: var(--fw-bold);
    font-family: 'Source Sans Pro', 'Gill Sans', 'Trebuchet MS', 'Arial', sans-serif;
    margin-bottom: .5rem;
    line-height: 1.2;
}
&__type {
    font-size: .8rem;
    font-weight: var(--fw-regular);
    margin-bottom: 0;
}
&__icon &:not(".interest-item--tight") {
    position: absolute;
    right: 1rem;
    margin-top: -.5rem;
svg {
      fill: white;
      width: 1.3rem;
      height: 1.3rem;
      background-color: rgb(255, 220, 162);
      border: 3px solid rgb(255, 220, 162);
      border-radius: 50%;
}
}
&\--tight {
    margin: .25rem;
    padding: .75rem 1rem;
.interest-item__name {
      margin-bottom: 0;
}
}
&\--pathway {
    padding-right: 4rem;
    border-left-color: rgb(var(--col-emerald-mid-2));
.interest-item__icon {
      position: absolute;
      right: calc(var(--vr) / 2);
      margin-top: -1rem;
      top: 50%;
      color: rgb(var(--col-emerald-mid-2));
.push-icon__icon {
        width: 1rem;
        height: 1rem;
}
}
}
&\--bachelor {
    border-color: rgb(var(--col-teal-mid-1));
}
&\--master {
    border-color: rgb(var(--col-orange-mid-1));
}
&\--doctor {
    border-color: rgb(var(--col-orange-mid-2));
}
&\--graddip {
    border-color: rgb(var(--col-emerald-mid-2));
}
&\--gradcert {
    border-color: rgb(var(--col-yellow-mid-2));
}
&\--diploma {
    border-color: rgb(var(--col-green-mid-2));
}
&\--short {
    border-color: rgb(var(--col-pink-mid-1));
}
}
.interest--tight {
.interest-item {
    padding: .75rem 1rem;
&__name {
      margin-bottom: 0;
}
}
}
